        margin: 42rem 0 0 0;
        text-align: left;
        max-width: 70rem; } }

.barra {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .barra {
      flex-direction: row;
      justify-content: space-between; } }
  .barra .logo {
    width: 30rem; }
    @media (min-width: 768px) {
