  @media (min-width: 768px) {
    .navegacion {
      visibility: visible;
      height: auto;
      opacity: 1;
      flex-direction: row; } }
  .navegacion a {
    color: #FFFFFF;
    margin-bottom: 1.3rem;
    font-size: 2rem; }
    .navegacion a:last-of-type {
      margin-bottom: 0; }
    .navegacion a:hover {
      color: #71b100; }
    @media (min-width: 768px) {
      .navegacion a {
        margin-bottom: 0;
        margin-right: 2.5rem;
        font-size: 1.4rem; }
        .navegacion a:last-of-type {
          margin-right: 0; } }
    @media (min-width: 1200px) {
