/* nosotros */

.nosotros{
    @include tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        column-gap: 2rem;
    }

    img{
        width: 100%;
    }

    .nosotros-contenido{
        blockquote{
            margin-left: 0;
            margin-top: 1rem;
            font-weight: 700;
            font-size: 2.2rem;
        }
    }
}