/*header -- YA LO INCLUÍ EN EL APP.SCSS*/

.header{
    background-color: $grisOscuro;
    padding: 2rem 0 3rem 0;
}

.webp .header.principal{
    background-image:url(../img/header.webp);
}

.no-webp .header.principal{
    background-image:url(../img/header.jpg);
}

.header.principal{
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    @include tablet{
        height: 75rem;
    }

    h1{
        color: $blanco;
        text-align: center;
        margin-top: 6.5rem;

        @include tablet{
            line-height: 1.8;
            margin: 42rem 0 0 0;
            text-align: left;
            max-width: 70rem;
        }
    }
}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet{
        flex-direction: row;
        justify-content: space-between;
    }

    .logo{
        width: 30rem;

        @include tablet{
            width: 20rem;
        }

        @include desktop{
            width: 30rem;
        }
    }

    #btn-barras{
        width: 4rem;
        margin: 1.5rem 0;
        &:hover{
            cursor: pointer;
        }

        @include tablet{
            display: none;
        }
    }      
}

.navegacion{
    display: flex;
    visibility: hidden;
    height: 0;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear;

    flex-direction: column;
    text-align: center;

    @include tablet{
        visibility: visible;
        height: auto;
        opacity: 1;
        flex-direction: row;
    }

    a{
        color: $blanco;
        margin-bottom: 1.3rem;
        font-size: 2rem;
        &:last-of-type{
            margin-bottom: 0;
        }

        &:hover{
            color: $verde;
        }

        @include tablet{
            margin-bottom: 0;
            margin-right: 2.5rem;
            font-size: 1.4rem;

            &:last-of-type{
                margin-right: 0;
            }
        }

        @include desktop{
            font-size: 1.8rem;
        }
    }
}

.navegacion.seleccionado{
    visibility: visible;
    height: auto;
    opacity: 1;
}


.dark-mode{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;

    #btn-dark{
        width: 3rem;
        filter: invert(100%);

        &:hover{
            cursor: pointer;
        }
    }
}