/* listado */

.anuncios{
    @include tablet{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 2rem;
    }
}

.anuncio{
    margin-bottom: 2rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include tablet{
        margin-bottom: 0;
    }
    img{
        display: block;
    }
    .atributos{
        @include tablet{
            width: 100%;
        }
    }
}

.anuncio-interna{
    .atributos{
        @include tablet{
            width: 40%;
        }
    }
    p:not(.precio){
        margin: .5rem 0 1rem 0;
    }
}

.anuncio-contenido{
    padding: 1rem 1.5rem 2rem 1.5rem;
    background-color: lighten($grisClaro, 5%);
    border: 1px solid $grisClaro;
    h3{
        font-weight: 300;
        margin: 0 0 1rem 0;
        text-align: center;
    }
    p:not(.precio){
        margin-bottom: 1.5rem;
    }

    a{
        margin-top: 2rem;
    }
}


.precio{
    font-size: 3rem;
    font-weight: 700;
    color: $verde;
    margin: 0;
    line-height: 1.4;
}


.atributos{
    margin-top: 1.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @include telefono{
        width: 70%;
    }
    .atributo{
        display: flex;
        justify-content: center;
        align-items: center;

        @include telefono{
            justify-content: flex-start;
        }

        p{
            margin: 0 0 0 1rem;
            font-weight: 900;
            font-size: 1.6rem;

            @include portrait{
                font-size: 1.8rem;
            }
        }
        img{
            width: 3rem;
            @include portrait{
                width: auto;
            }
        }
    }
}

.todas{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 4rem;

    a{
        min-width: 30%;
        text-transform: uppercase;
    }
}