/* dark */

body.dark{
    background-color: darken($grisOscuro, 10);
    color: $blanco;

    //Anuncio
    .anuncio-contenido{
        background-color: $grisOscuro;
        border-color: $grisOscuro;
    }

    .atributos{
        .atributo{
            img{
                filter: invert(100%);
            }
        }
    }

    //Blog
    .entrada-contenido{
        a{
            color: $blanco;
        }
    }

    //Admin
    .lista-propiedades, .lista-vendedores, .vendedor, .propiedad{
        border-color: lighten($grisOscuro, 10);
    }

    .propiedad-informacion{
        color: $blanco;
    }

    //Fomularios
    .formulario{
        .contenedor-campos{
            border-color: $grisClaro;
            
            legend{
                color: $grisClaro;
            }
        }

        .campo{
            label, input, select{
                color: $grisClaro;
            }

            input, textarea, select{
                border-color: $grisClaro;
                background-color: $grisOscuro;
                border-color: $grisOscuro;
            }
        }
    }
}