/* utilidades */

//Botones
.btn{
    display: inline-block;
    padding: 1.3rem 1.8rem;
    font-weight: 700;
    text-align: center;
    border: none;

    &:hover{
        cursor: pointer;
    }
}

.btn-naranja{
    background-color: $naranja;
    color: $blanco;

    &:hover{
        background-color: darken($naranja, 4%);
    }
}

.btn-verde{
    background-color: $verde;
    color: $blanco;

    &:hover{
        background-color: darken($verde, 4%);
    }
}

.btn-azul{
    background-color: $azulClaro;
    color: $blanco;

    &:hover{
        background-color: darken($azulClaro, 6%);
    }
}

.btn-rojo{
    background-color: $rojo;
    color:$blanco;

    &:hover{
        background-color: darken($rojo, 5%);
    }
}

.btn-azul-vivo{
    background-color: $azulVivo;
    color: $blanco;

    &:hover{
        background-color: darken($azulVivo, 6%);
    }
}

.btn-light{
    padding: .7rem 1.5rem;
    font-weight: 400;
    border-radius: .5rem;
}

//Contenedor
.contenedor{
    max-width: 1200px;
    width: 90%;
    margin: 0 auto;
}


//Texto
.text-center{
    text-align: center;
}


//Widths
.w-100{
    width: 100%;
}

.fw-300{
    font-weight: 300;
}

.seccion{
    margin-top: 4rem;
    margin-bottom: 4rem;
}

.dp-block{
    display: block;
}

.contenido-centrado{
    max-width: 80rem;
}

.w-50{
    max-width: 50rem;  
}

//Aletas

.alerta{
    padding: 1rem;
    text-align: center;
    font-weight: 700;
    border-radius: 1rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
}

.error{
    color: $rojoOscuro;
    border: 2px solid $rojoOscuro;
    background-color: $rosado;
}

.exito{
    background-color: $verde;
    color: $blanco;
}