/* iconos */

.iconos{
    margin-top: 5rem;
    @include tablet{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 2rem;
    }
}

.icono{
    text-align: center;
    margin-bottom: 6rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include telefono{
        margin-bottom: 0;
    }

    img{
        max-width: 12rem; 
    }

    h3{
        margin: 1rem 0 1.5rem 0;
        font-weight: 300;
        text-transform: uppercase;
    }
}
