/* vendedores */

.lista-vendedores{
    margin-top: 2rem;
    border-top: 1px solid $grisClaro;
    border-bottom: 1px solid $grisClaro;
}

.vendedor{
    padding: 2rem;
    border-bottom: 1px solid $grisClaro;

    &:last-of-type{
        border-bottom: none;
    }

    @include tablet{
        display: grid;
        grid-template-columns: 3fr 1fr;
        column-gap: 1.5rem;
    }
    .vendedor-contenido{
        h3{
            font-size: 2.5rem;
            margin: 0 0 .5rem 0;
        }

        .vendedor-item{
            display: flex;
            align-items: center;

            p{
                font-size: 1.6rem;
                line-height:normal;
                margin: 0 0 0 .5rem;
                padding-bottom: .3rem;
            }
        }
        margin-bottom: .5rem;
    }

    .vendedor-botones{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;

        @include tablet{
            display: flex;
            flex-direction: column;
            justify-content: center;

            a{
                margin-bottom: 1.5rem;
            }
        }

        form{
            input[type="submit"]{
                width: 100%;
            }
        }
    }
}