/* formulario */

.formulario{
    margin-top: 2rem;

    input[type="submit"]{
        margin-top: 2rem;
    }
}

.contenedor-campos{
    padding: 2rem;
    margin-bottom: 2rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    legend{
        font-weight: 700;
        font-size: 2rem;
    }
    .campo{
        margin-bottom: 1.5rem;

        &:last-of-type{
            margin-bottom: 0;
        }

        label{
            display: block;
            margin-bottom: .5rem;
            font-weight: 700;
            color: $grisOscuro;
        }

        input, textarea, select{
            width: 100%;
            padding: 1rem;
            border-radius: 1rem;
            border: 2px solid $grisClaro;
        }

        textarea{
            height: 15rem;
            resize: none;
        }

        select{
            &:hover{
                cursor: pointer;
            }
        }
    }
}

.formulario-imagen{
    max-width: 15rem;
    margin-bottom: 2rem;
}

.display-right{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

/*.comunicacion*/

.contacto{
    p{
        font-size: 1.4rem;
        margin-top: 0;
    }
}

.comunicacion-contacto{
    max-width: 30rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
}

.comunicacion{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    label{
        font-weight: 700;
        text-transform: uppercase;
        margin-right: 1rem;
    }
}

#externos{
    margin-top: 2rem;
}