/* blog */
.blog{
    h3{
        margin-top: 0;
    }
}

.entrada{
    margin-bottom: 4rem;
    width: 100%;

    &:last-of-type{
        margin-bottom: 0;
    }
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    img{
        width: 100%;
    }
}

.entrada-contenido{
    a{
        color: $negro;
    }
    .entrada-titulo{
        margin: 1rem 0;

        &::after{
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
    p{
        margin: 1rem 0;
    }

    .entrada-informacion{
        span{
            color: $naranja;
        }
    }
}