/* admin */

.lista-propiedades{
    border-top: 1px solid $grisClaro;
    border-bottom: 1px solid $grisClaro;
}

.propiedad{
    padding: 2rem;
    border-bottom: 1px solid $grisClaro;

    &:last-of-type{
        border-bottom: none;
    }
    .propiedad-botones{
        width: 100%;
        margin-top:.5rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;

        input[type="submit"]{
            width: 100%;
        }
    }
}

.propiedad-contenido{
    padding-bottom: 1.8rem;
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
}

.propiedad-informacion{
    color: $negro;
    @include tablet{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    h3{
        margin: .5rem 0 1rem 0;
        font-size: 2.5rem;
        @include tablet{
            margin-top: 0;
        }
    }

    .precio{
        font-size: 2.2rem;
    }
}