html {
    box-sizing: border-box;
    font-size: 62.5%;
    height:100%;
}

*, *:before, *:after{
    box-sizing: inherit;
}

body{
    font-size: 1.6rem;
    font-family: $fuentePrincipal;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.flex-1{
    flex: 1;
}


p,blockquote{
    line-height: 1.8;
    font-size: 2rem;
}

a{
    text-decoration: none;
}

/*titulos*/
h1, h2, h3, h4{
    line-height: 1.4;
}


h1{
    font-size: 4rem;
}

h2{
    font-size: 3.5rem;
}

h3{
    font-size: 3rem;
}

h4{
    font-size: 2.5rem;
}

ul{
    padding: 0;
    list-style: none;
}

img{
    max-width: 100%;
}
