/* testimonial */

.testimonial{
    margin-top: 4rem;
    @include tablet{
        margin-top: 0;
    }

    h3{
        margin-top: 0;
    }
}

.testimonial-contenido{
    background-color: $verde;
    color: $blanco;
    padding: 2rem;
    border-radius: 1.5rem;
    blockquote{
        font-size: 2.4rem;
        padding-left: 5rem;
        position: relative;
        &::before{
            content: '';
            display: block;
            position: absolute;
            width: 7rem;
            height: 5rem;
            background-image: url(../img/comilla.svg);
            background-position: center center;
            background-repeat: no-repeat;
            left: -2rem;
        }
    }

    .testimonial-autor{
        text-align: right;
    }
}