/* mixins */

@mixin portrait{
    @media(min-width: 320px){
        @content;
    }
}

@mixin telefono{
    @media(min-width: 480px){
        @content;
    }
}

@mixin tablet{
    @media(min-width: 768px){
        @content;
    }
}

@mixin desktop{
    @media(min-width: 1200px){
        @content;
    }
}